import React, {Component} from "react";
import {Checkbox, Form, Input, Radio, Select, Button} from "antd";
import {withRouter} from "react-router-dom";
import {observable, action, runInAction} from "mobx";
import {observer} from "mobx-react";
import PropTypes from 'prop-types'

const FormItem = Form.Item;
const RadioGroup = Radio.Group;
const Option = Select.Option;

@observer
class UserForm extends Component {
  @observable loading = false;

  @action setLoading (status) {
    this.loading = status;
  }

  static propTypes = {
    match: PropTypes.object.isRequired,
    location: PropTypes.object.isRequired,
    history: PropTypes.object.isRequired,
    user: PropTypes.object
  }

  componentWillMount () {
    console.log(this.props)
  }

  Submit = (e) => {
    e.preventDefault();

    this.setLoading(true);
    this.props.form.validateFields((err, values) => {
      if (!err) {
        setTimeout(() => {
          runInAction(() => {
            this.setLoading(false)
          })
        }, 2000)
      }
    });
  }

  render () {

    const formItemLayout = {
      labelCol: {span: 6},
      wrapperCol: {span: 14},
    };

    const roles = [
      {name: "", id: 0},
      {name: "", id: 1},
      {name: "", id: 2},
      {name: "", id: 3},
      {name: "", id: 4}
    ];
    const {getFieldDecorator} = this.props.form;
    return (<Form onSubmit={this.Submit} style={{margin: "8px 0"}}>
      <FormItem
        {...formItemLayout}
        label=""
      >
        {getFieldDecorator('name', {
          rules: [{required: true, message: '!'}],
        })(
          <Input placeholder=""/>
        )}
      </FormItem>

      <FormItem
        {...formItemLayout}
        label=""
      >
        {getFieldDecorator('email', {
          rules: [{required: true, message: '!'}],
        })(
          <Input placeholder=""/>
        )}
      </FormItem>

      <FormItem
        {...formItemLayout}
        label=""
      >
        {getFieldDecorator('phone', {
          rules: [{required: true, message: '!'}],
        })(
          <Input placeholder=""/>
        )}
      </FormItem>

      <FormItem
        {...formItemLayout}
        label=""
      >
        {getFieldDecorator('sex', {
          rules: [{required: true}]
        })(
          <RadioGroup>
            <Radio value={1}></Radio>
            <Radio value={0}></Radio>
          </RadioGroup>
        )}
      </FormItem>

      <FormItem
        {...formItemLayout}
        label=""
      >
        {getFieldDecorator('no', {
          rules: [{required: true, message: '!'}],
        })(
          <Input placeholder=""/>
        )}
      </FormItem>

      <FormItem
        {...formItemLayout}
        label=""
      >
        {getFieldDecorator('department', {
          rules: [{required: true, message: '!'}]
        })(
          <Select style={{width: 160}}>
            <Option value={0}></Option>
            <Option value={1}></Option>
            <Option value={2}></Option>
            <Option value={3}></Option>
            <Option value={4}></Option>
          </Select>
        )}
      </FormItem>

      <FormItem
        {...formItemLayout}
        label=""
      >
        {getFieldDecorator('roles', {
          rules: [{required: true, message: '!'}]
        })(
          <Checkbox.Group>
            {roles.map(item => (
              <Checkbox key={item.id} value={item.id}>{item.name}</Checkbox>
            ))}
          </Checkbox.Group>
        )}
      </FormItem>

      <FormItem
        wrapperCol={{span: 12, offset: 6}}
      >
        <Button loading={this.loading} type="primary" htmlType="submit">Submit</Button>
        <Button type="primary" style={{marginLeft: 15}} onClick={this.props.history.goBack}>Back</Button>
      </FormItem>
    </Form>)
  }
}

export default Form.create({
  mapPropsToFields (props) {
    const {user} = props
    return {
      name: Form.createFormField({
        value: user.name
      }),
      email: Form.createFormField({
        value: user.email
      }),
      phone: Form.createFormField({
        value: user.phone
      }),
      sex: Form.createFormField({
        value: user.sex || 1
      }),
      no: Form.createFormField({
        value: user.no
      }),
      department: Form.createFormField({
        value: user.department || 0
      }),
      roles:Form.createFormField({
        value: user.roles
      })
    }
  }
})(withRouter(UserForm));



